.header {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding-top: 10px;
  padding-bottom: 15px;
}
@media (min-width: 768px) {
  .header {
    padding-top: 18px;
    padding-bottom: 18px;
  }
}
.header__hamburger {
  margin-right: auto;
  padding-right: 28px;
}
@media (min-width: 768px) {
  .header__hamburger {
    margin-right: 48px;
    padding-right: 0;
  }
}
.header-menu {
  display: none;
}
@media (min-width: 768px) {
  .header-menu {
    margin-right: auto;
    display: flex;
    gap: 30px;
  }
}
@media (min-width: 768px) {
  .header-menu__item:last-child {
    display: none;
  }
}
@media (min-width: 1200px) {
  .header-menu__item:last-child {
    display: block;
  }
}
.header-menu__link {
  font-size: 14px;
}
@media (min-width: 1200px) {
  .header-menu__link {
    font-size: 15px;
  }
}
.header-info {
  display: flex;
  align-items: center;
  margin-left: auto;
}
.header-info__contacts {
  display: none;
}
@media (min-width: 768px) {
  .header-info__contacts {
    display: block;
    margin-left: 48px;
    min-width: 132px;
  }
}
@media (min-width: 1200px) {
  .header-info__contacts {
    margin-left: 58px;
  }
}
.header-info__contacts a {
  display: block;
}
.header-info__contacts a:first-child {
  font-size: 15px;
  font-weight: 500;
}
.header-info__contacts a:last-child {
  color: #797986;
  font-size: 13px;
}
.header-info__buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 51px;
}
.header__btn {
  display: none;
}
@media (min-width: 1200px) {
  .header__btn {
    display: block;
    margin-left: 59px;
  }
}

@media (min-width: 768px) {
  .logo-mobile {
    display: none;
  }
}
@media (min-width: 1200px) {
  .logo-mobile {
    display: block;
  }
}

.logo-tab {
  display: none;
}
@media (min-width: 768px) {
  .logo-tab {
    display: block;
  }
}
@media (min-width: 1200px) {
  .logo-tab {
    display: none;
  }
}

.contacts-top {
  background: #f5f5f5;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contacts-top__title {
  width: 100%;
  position: relative;
  font-size: 22px;
  font-weight: 400;
  font-family: "RoadRadio";
  line-height: 26px;
  padding-top: 20px;
  padding-bottom: 20px;
}
@media (min-width: 768px) {
  .contacts-top__title {
    font-size: 32px;
    padding-top: 15px;
    padding-bottom: 15px;
  }
}
@media (min-width: 1200px) {
  .contacts-top__title {
    font-size: 46px;
    padding-top: 24px;
    padding-bottom: 30px;
  }
}
.contacts-top__title::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: #000;
  opacity: 0.1;
}
.contacts-wrapper {
  padding-top: 30px;
  padding-bottom: 50px;
}
@media (min-width: 768px) {
  .contacts-wrapper {
    display: flex;
    justify-content: space-between;
    padding-top: 40px;
  }
}
@media (min-width: 1200px) {
  .contacts-wrapper {
    padding-top: 62px;
    padding-bottom: 66px;
  }
}

.info {
  width: 287px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 50px;
}
@media (min-width: 768px) {
  .info {
    margin-left: 56px;
    margin-bottom: 0;
  }
}
@media (min-width: 1200px) {
  .info {
    max-width: 490px;
    width: 100%;
    margin-right: 0;
  }
}
.info__number {
  display: block;
  font-size: 28px;
  font-family: "RoadRadio";
  margin-bottom: 6px;
}
.info__mail {
  display: block;
  font-size: 25px;
  color: #bf2738;
  font-family: "Golos";
  margin-bottom: 20px;
}
.info__desc {
  font-family: "Golos";
  font-weight: 400;
}

.reqs {
  padding-top: 20px;
  display: grid;
  gap: 25px;
}
.reqs-row, .reqs-row p {
  color: #808080;
  font-size: 13px;
}

.form {
  max-width: 287px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 768px) {
  .form {
    max-width: 350px;
  }
}
@media (min-width: 1200px) {
  .form {
    max-width: 630px;
    margin-left: 0;
    margin-right: 0;
  }
}
.form__group {
  display: grid;
  grid-row-gap: 10px;
  margin-bottom: 20px;
}
@media (min-width: 1200px) {
  .form__group {
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 30px;
    grid-row-gap: 30px;
  }
}
.form__title {
  font-size: 20px;
  font-family: "RoadRadio";
  margin-bottom: 10px;
}
.form__input {
  background: #f5f5f5;
  border-radius: 14px;
  font-family: "Golos";
  font-size: 16px;
  line-height: 24px;
  color: #797986;
  padding: 15px 20px 17px;
  transition: all 0.3s ease 0s;
  width: 100%;
  border: 2px solid transparent;
}
.form__input:hover, .form__input:focus {
  background-color: #fff;
  border: 2px solid #e6e6e6;
}
.form__label {
  position: relative;
  margin-left: 35px;
  margin-top: 30px;
  font-size: 13px;
  color: #808080;
  cursor: pointer;
  transition: all 0.15s ease-in-out;
}
.form__label:hover .checkbox, .form__label:focus .checkbox {
  background-image: url(../../img/checkbox-hover.svg);
}
.form__field small {
  font-size: 12px;
  color: #bf2738;
}
.form__checkbox:checked + .checkbox {
  background-image: url(../../img/ckeckbox-on.svg);
}
.form__textarea {
  min-height: 166px;
}
@media (min-width: 1200px) {
  .form__textarea {
    grid-column: 1/3;
  }
}
@media (min-width: 1200px) {
  .form__textarea {
    min-height: 133px;
  }
}
.form-btn {
  margin-top: 20px;
  display: block;
}

.checkbox {
  position: absolute;
  margin-left: -35px;
  width: 20px;
  height: 20px;
  background-image: url(../../img/checkbox-off.svg);
  border-radius: 4px;
  transition: all 0.15s ease-in-out;
}
.checkbox-span {
  font-size: 13px;
}
.checkbox-span--gray {
  color: #808080;
}

.breadcrumps {
  display: flex;
  padding-top: 16px;
  padding-bottom: 16px;
  gap: 27px;
}
@media (min-width: 1200px) {
  .breadcrumps {
    padding-top: 21px;
    padding-bottom: 21px;
  }
}
.breadcrumps__item:first-child {
  position: relative;
}
.breadcrumps__item:first-child::after {
  position: absolute;
  content: "";
  top: 45%;
  transform: translateY(50%);
  right: -13.5px;
  width: 3px;
  height: 3px;
  border-radius: 50%;
  background-color: #797986;
}
.breadcrumps__link {
  color: #797986;
  font-size: 12px;
  line-height: 14px;
}
.breadcrumps__link--active {
  color: #c9c9cf;
}

.map {
  width: 100%;
  height: 477px;
}
@media (min-width: 768px) {
  .map {
    height: 480px;
  }
}
@media (min-width: 1200px) {
  .map {
    height: 599px;
  }
}

[class*=copyrights-pane] {
  display: none !important;
}

[class*=ground-pane] {
  filter: grayscale(1);
}

#name.input-error,
#phone.input-error {
  background: rgba(191, 39, 56, 0.12);
}
#name.input-error::placeholder,
#phone.input-error::placeholder {
  color: #bf2738;
}

.address__list {
  display: grid;
  grid-row-gap: 10px;
  padding-top: 30px;
  padding-bottom: 60px;
  max-width: 710px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 768px) {
  .address__list {
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 10px;
    padding-top: 60px;
    padding-bottom: 80px;
  }
}
@media (min-width: 1200px) {
  .address__list {
    grid-template-columns: repeat(4, minmax(250px, 300px));
    grid-column-gap: 30px;
    grid-row-gap: 30px;
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 80px;
    padding-bottom: 100px;
  }
}
.address__item {
  display: flex;
  flex-direction: column;
  max-width: 350px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  background: #f5f5f5;
  border-radius: 20px;
  padding-top: 33px;
  padding-left: 12px;
  padding-right: 12px;
  padding-bottom: 40px;
  font-family: "Golos";
}
@media (min-width: 768px) {
  .address__item {
    padding-left: 22px;
    padding-right: 16px;
  }
}
@media (min-width: 768px) {
  .address__item:last-child {
    grid-column: 1/3;
    max-width: 710px;
    width: 100%;
  }
  .address__item:last-child .address__numbers {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }
}
@media (min-width: 1200px) {
  .address__item:last-child {
    grid-column: 3/5;
  }
}
.address__item a {
  display: block;
}
.address__title {
  margin-bottom: 15px;
  font-family: "RoadRadio";
  font-size: 26px;
  font-weight: 400;
}
.address__numbers {
  margin-bottom: 10px;
  font-weight: 400;
  font-size: 19px;
}
.address__desc {
  color: #797986;
  margin-bottom: 10px;
}
.address__dir {
  color: #bf2738;
  margin-bottom: 20px;
}
.address__time {
  margin-top: auto;
}

.footer-wrapper {
  display: grid;
}
@media (min-width: 768px) {
  .footer-wrapper {
    grid-template-columns: repeat(2, 1fr);
  }
}
.footer__row {
  padding: 40px 15px;
  background-color: #222225;
}
@media (min-width: 1200px) {
  .footer__row {
