.footer__row .menu a {
  font-family: "Akzidenz-Grotesk Pro";
  font-weight: 400;
  font-size: 16px;
  line-height: 29px;
  color: #797986;
  transition: color 0.15s ease-in-out;
}
.footer__row .menu a:hover,
.footer__row .menu a:focus {
