}
.footer-info__numbers {
  display: grid;
  margin-bottom: 20px;
}
@media (min-width: 1200px) {
  .footer-info__numbers {
    margin-bottom: 0;
  }
}
.footer-info__numbers a {
  font-family: "Golos";
  font-style: normal;
  font-weight: 500;
  font-size: 23px;
  line-height: 34px;
  color: #fff;
}
.footer__btn {
  border: 2px solid #404040;
  background-color: transparent;
  margin-bottom: 15px;
}
.footer__btn:hover, .footer__btn:focus {
  border-color: #fff;
}
.footer__btn:active {
  background: #222225;
}
@media (min-width: 1200px) {
  .footer__btn {
    margin-bottom: auto;
  }
}
.footer__mail {
  display: block;
  color: #f23147;
  font-size: 19px;
  line-height: 20px;
  font-family: "Akzidenz-Grotesk Pro";
  font-weight: 500;
  margin-bottom: 33px;
}
@media (min-width: 1200px) {
  .footer__mail {
    font-weight: 400;
    font-size: 24px;
  }
}
.footer__address {
  color: #fff;
  font-family: "Inter";
  max-width: 290px;
  width: 100%;
}
@media (min-width: 1200px) {
  .footer__address {
    max-width: 447px;
    margin-top: 97px;
  }
}
.footer-bottom {
