@media (min-width: 1200px) {
  .footer .row-middle {
    padding-left: 40px;
  }
}
@media (min-width: 1200px) and (min-width: 1200px) {
  .footer .row-middle {
    padding-left: calc(12.0833333333vw - 105px);
  }
